<template>
    <el-row class="head">
        <el-col :span="4" class="logo">
            <img src="../assets/logo.png" alt="">
            <span class="name"></span>
        </el-col>
    </el-row>
</template>
<script>
export default {
    name: 'toolbar'
}
</script>
<style scoped>
.head {
    position: fixed;
    top: 0;
    width: 100%;
    height: 60px;
    background: #D6DDE4;
    z-index: 99;
}
.logo{
    height: 60px;
    line-height: 60px;
    padding: 0 10px;
    border-bottom: 1px solid #475669;
    background: #324157;
}
.logo img { 
    height: 50px;
    vertical-align: middle;
}
.logo .name {
    font-size: 30px;
    color: #fff;
    margin-left: 10px;
    vertical-align: middle;
}
</style>





