import Vue from 'vue'
import axios from 'axios'
import router from './router'
import Element from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import App from './App.vue'

Vue.use(Element);
Vue.prototype.$axios = axios;

// 
Vue.prototype.$store = new Vue({
    data: {
        counts: {
            user: 0,
            article: 0,
            comment: 0,
        }
    },
    methods: {
        updateCount (type) {
            this.counts[type] -= 1;
        },
    },
    created () {
        this.$axios.get('http://127.0.0.1:3000/admin/getartcount')
            .then(({data}) => { 
                this.counts.article = data.result.count;
            }).catch((res) => { console.log(res); });

        this.$axios.get('http://127.0.0.1:3000/admin/getusercount')
            .then(({data}) => { 
                this.counts.user = data.result.count; 
            }).catch((res) => { console.log(res); });
        
        this.$axios.get('http://127.0.0.1:3000/admin/getcommcount')
            .then(({data}) => { 
                this.counts.comment = data.result.count;
            }).catch((res) => { console.log(res) });
    },
});

//  APP 
new Vue({
    el: '#app',
    router,
    render: h => h(App)
});



