import Vue from 'vue'
import Router from 'Vue-router'
// import Main from '../components/Main.vue'
// import User from '../components/User.vue'
// import Article from '../components/Article.vue'
// import Comment from '../components/Comment.vue'
// import addUser from '../components/addUser.vue'

Vue.use(Router)

const Main = resolve => require(['../components/Main.vue'], resolve)
const User = resolve => require(['../components/User.vue'], resolve)
const Article = resolve => require(['../components/Article.vue'], resolve)
const Comment = resolve => require(['../components/Comment.vue'], resolve)

export default new Router({
    routes: [
        {
            path: '/',
            name: 'Main',
            component: Main,
        },
        {
            path: '/user',
            name: 'User',
            component: User,
        },
        {
            path: '/article',
            name: 'Article',
            component: Article,
        },
        {
            path: '/comment',
            name: 'Comment',
            component: Comment,
        }
    ]
});


